// tollActions.js
import * as actionTypes from './actionTypes';

export const addToll = (tollData) => {
    return {
        type: actionTypes.ADD_TOLL,
        payload: tollData,
    };
};

export const removeToll = (tollId) => {
    return {
        type: actionTypes.REMOVE_TOLL,
        payload: tollId,
    };
};
